<template>
	<transition name="fade" mode="in-out">
		<div class="articleDetailsPage">
			<v-Header class="head">
				<span slot='text' class="headText" v-html='this.article.title'></span>
				<mt-button v-if='showmore' icon="more" class='more' @click='showMenu' slot="right"></mt-button>
			</v-Header>
			<div id='articleScrollCon' v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
				<div style="min-height:101%;background: white;">
					<div class="articleDetailsCon" v-if= 'this.article.title'>
						<!-- <div class="articleTagCon" v-html='`${this.article.nickname} ${this.article.publishDate}`'></div> -->
						<div class="articleTextCon">
							<img v-if='this.article.preimg' :src="this.article.preimg">
							<div class="articleTitleCon" v-html='this.article.title'></div>
              <div class='userInfoCon' @click='gotopath(userId,userType)'>
                  <div class='headimg'><img :src="this.headimg" width='100%'></div>  
                  {{this.nickname}}              
              </div>
              <div v-html='this.article.cont'></div>
						</div>
					</div>
          <div class="giveTheThumbsUp" v-if='this.article.title' @click="addPraise">
						<div class="addPraiseBtn"></div>{{this.article.likeNum}}
					</div>
          <v-loadmove v-if='!this.article.title' type='more'/>
					<hr/>
					<v-CommentList :CommentList='CommentList' :entry='entry' :getReplyList='getReplyList'/>
          <v-loadmove v-if='CommentList.length>0' :type='loadType'/>
				</div>
			</div>
			<div class="commentInputCon">
				<input type="text" placeholder="快来说点什么吧！">
				<div class='inputBtn' @click="entry(articleId,0)"></div>
				<div :class='["addPraiseBtn",this.article.like==2?"active2":"active1"]' @click="addPraise"></div>
			</div>
			<mt-actionsheet :actions="actions" v-model="reclaimsheetVisible"></mt-actionsheet>
      <mt-popup class='popup' v-model="popupVisible" position="bottom">
        <v-showReplyComment :Lists='Lists' :entry='entry' />
      </mt-popup>
      <mt-popup class='reclaimPage' v-model="showreclaimPage" :modal=false position="right">
        <v-reclaimPage :comeBack='comeBack' :reclaimId='reclaimId'/>
      </mt-popup>
			<mt-popup class='addCommentpopup' :modal=false v-model="addCommentVisible" position="right">
        <v-commentpage :articleId='sendCommentId' :atUserId='sendCommentatUserId' :comeBack='Back' v-on:update-comment-list="updateCommentList"/>
      </mt-popup>
      <mt-actionsheet :actions="delCommentActions" v-model="delCommentVisible"></mt-actionsheet>
			<router-view/>
		</div>
	</transition>
</template>
<script>
	import Header from '../../components/header';
	import CommentList from '../articleDetails/commentList';
	import reclaimPage from '../articleDetails/reclaimPage';
  import commentPage from '../articleDetails/commentPage';
	import showReplyComment from '../articleDetails/showReplyComment';
  import loadMove1 from '../../components/seeOcean/loadMore';
	import { mapState,mapMutations } from 'vuex';
	export default {
		name: '',
		components: {
			'v-Header': Header,
			'v-CommentList': CommentList,
			'v-reclaimPage': reclaimPage,
      'v-loadmove': loadMove1,
      'v-commentpage': commentPage,
      'v-showReplyComment': showReplyComment,
		},
		props: {},
		data() {
			return {
				delCommentActions: [{
					name: '删除评论',
					method: this.delComment
				}],
        delCommentInfo:{},
				delCommentVisible: false,
        addCommentVisible: false,
				reclaimsheetVisible: false,
				articleId:this.$route.params.contId,
				article:{},
				CommentList: [],
        sendCommentId:'',
        sendCommentatUserId:'',
        lastCommentId:0,
        atUserId:this.$route.params.atUserId,
        userId:'',
        userType :'',
        headimg  :'',
        nickname :'',
        reclaimId:null,
        showreclaimPage:false,
        loadType:'more',
				loading:false,
				popupVisible:false,
				commentId:null,
				Lists:{},
				contType:'',
				replyTop:0
			}
		},
		watch: {
      delCommentVisible(v,o){
        if(!v){
          this.delCommentInfo = {};
        }
      }
    },
		computed: {
      ...mapState({
        _userid:state => state.userInfo.userId,//当前用户id
      }),
      showmore(){
        return this._userid&&this._userid == this.userId;
      },
      actions(){
        if(this.article.contStatus == 2){
         return (
          [{
            name: '编辑',
            method: this.updatePage
          }, {
            name: '下架',
            method: this.reclaim
          }])
        }else if(this.article.contStatus == 3){
          return ([{
            name: '编辑',
            method: this.updatePage
          }])
        }
      }
    },
		created() {
			this.getArticle();
			this.getComment();
		},
    updated() {
			let replyTop = document.getElementsByClassName('commentlistsCon')[0].offsetTop;
			if(this.$store.state.userInfo.Reply&&this.replyTop!=replyTop){		
				this.replyTop = replyTop;
				document.getElementById('articleScrollCon').scrollTop=replyTop;
			}
		},
		beforeDestroy(){
			this.setReply({Reply:null})
		},
		methods: {
			...mapMutations(['setReply']),
      gotopath(id,type){
        if(type==2){
           this.$router.push({path:'/detail/expert',query:{expertId:id}});
        }else if(type==3){
           this.$router.push({path:'/detail/coach',query:{coachId:id}});
        }else if(type==4){
          this.$router.push({path:'/detail/club',query:{clubId:id}});
        }
      },
      Back(){
        this.addCommentVisible=false;
      },
			delComment(){
        this.popupVisible = false;
        const _url = this.delCommentInfo.type == 'reply'?`${this.$port.COMMENT_DEL_REPLY}?replyId=${this.delCommentInfo.id}`:`${this.$port.COMMENT_DEL}?commentId=${this.delCommentInfo.id}`;
        this.$http.post(`${_url}`,{
        }).then((response) => {
          const {data} = response;
          if(response.code == 200){
            this.updateCommentList();
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
			},
			updateCommentList(){
        this.loading = false;
        this.loadType = 'more';
        this.lastCommentId = 0;
        this.getComment('ture');
        document.getElementById('articleScrollCon').scrollTop=document.getElementsByClassName('commentlistsCon')[0].offsetTop;
      },
			entry(id=this.articleId,num=0,type) {
        if(num!=0&&num==this._userid){
					this.delCommentVisible = true;
          this.delCommentInfo = type;
        }else{
          this.popupVisible = false;
          this.addCommentVisible=true;
          this.sendCommentId=id;
          this.sendCommentatUserId=num;
        }
				// this.$router.push(`commentPage/${this.articleId}/0`);
			},
			getReplyList(data){
        this.Lists = {...data};
        this.Lists.replyList = [];
        this.popupVisible = true;
        this.commentId = data.commentId;
        this.$http.get(this.$port.COMMENT_GET_REPLY,{
          params:{
            commentId:this.commentId,
            lastCommentReplyId:0
          }
        }).then((response) => {
          const {data} = response;
          if(response.code == 200){           
            this.Lists.replyList=data;
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
      },
      loadMore(){
        if(this.lastCommentId!=0){
          this.getComment();
        }
      },
      //获取文章评论
      getComment(isUpdate){
        if(this.loadType == 'all'||this.loading) return;
        this.loading = true;
        this.$http.get(this.$port.COMMENT_LIST_PROT,{
          params:{
            contId:this.articleId,
            lastCommentId:this.lastCommentId
          }
        }).then((response) => {
          const {data} = response;
          this.loading = false;
          if(response.code == 200){
            if(isUpdate){
              this.CommentList=[];
            }
            this.CommentList=[...this.CommentList,...data];
            if(data.length>0){
              this.lastCommentId=data.pop().commentId;
              this.loadType = 'more';
            }else{
              this.loadType = 'all';
            }
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error.response.data);
        });
      },
      //文章编辑or下架按钮显示
			showMenu() {
				this.reclaimsheetVisible = true;
			},
      //给文章点赞
			addPraise(){
				if(this.article.like==1){
					this.$http.post(`${this.$port.ADDLIKE_PROT}?contId=${this.articleId}`,{
					}).then((response) => {
						const {data} = response;
						if(response.code == 200){
							this.article.like=2;
              this.article.likeNum+=1;
						}
					})
					.catch((error) => {
						console.log(error.response.data);
					});
				}
			},
      //文章编辑
			updatePage() {
				this.$router.push({path:'/write/cover',query:{productId:this.articleId,contType:this.contType}});
			},
      //隐藏下架页
      comeBack(){
        this.showreclaimPage = false;
        this.reclaimId = null;
      },
      //显示下架页
			reclaim() {
				console.log('下架');
        this.showreclaimPage = true;
        this.reclaimId = this.articleId;
			},
      //获取文章信息
			getArticle() {
        this.$http.get(this.$port.ARTICLE_PROT,{
          params:{
            productId:this.articleId
          }
        }).then((response) => {
          const {data} = response;
          if(response.code == 200){
            this.userId = data.userId;
            this.userType = data.userType;
            this.headimg  = data.headimg;
            this.nickname  = data.nickname;
            this.contType  = data.contType;
						this.article={...this.article,...data};
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
			},
		}
	}
</script>
<style lang="scss" scoped>
	* {
		border: none;
		margin: 0;
		padding: 0;
	}

	.articleDetailsPage {
		width: 100%;
		height: auto;
		overflow: hidden;
		background: white;
		.head {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			.headText{
				display: inline-block;
				max-width: 5rem;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			.more {
				position: absolute;
				top: 0;
				right: 0;
				height: 0.8rem;
				width: 0.8rem;
				padding: 0;
				background: rgba(0, 0, 0, 0);
				box-shadow: none;
			}
		}
		#articleScrollCon {
			top: 0.82rem;
			left: 0;
			right: 0;
			bottom: 0.86rem;
			margin: auto;
			position: absolute;
			overflow-x: hidden;
			overflow-y: scroll;
			-webkit-overflow-scrolling: touch;
			background: white;
		}
		.articleDetailsCon {
			margin-top: 0.23rem;		
			.articleTagCon,
			.articleTextCon {
				width: 6.9rem;
				margin-left: 0.3rem;
			}
			.articleTitleCon {
				font-size: 0.44rem;
				font-weight: bold;
			}
      .userInfoCon{
        width:100%;
        height:1rem;
        margin:0.4rem 0;
        border-top:1px solid rgb(229,229,229);
        border-bottom:1px solid rgb(229,229,229);
        display:flex;
        align-items:center;
        .headimg{width:0.8rem;height:0.8rem;border-radius:0.8rem;overflow:hidden;margin:0 0.2rem;
        img{margin:0;}
        }
      }
			.articleTagCon {
				font-size: 0.22rem;
				color: #888888;
				// margin-top: 0.36rem;
			}
			.articleTextCon {
				font-size: 0.32rem;
				line-height: 0.62rem;
        word-wrap: break-word;
				// margin-top: 0.15rem;
				img {
					width: 100%;
					margin-top: 0.27rem;
				}
			}
		}
		.giveTheThumbsUp {
			width: 2.1rem;
			height: 0.72rem;
			margin: 0.6rem auto 0.32rem;
			background: #1797c6;
			border-radius: 0.72rem;
			font-size: 0.32rem;
			line-height: 0.72rem;
			text-align: center;
			color: white;
			display: flex;
			align-items: center;
			justify-content: center;
			.addPraiseBtn {
				margin-right: 0.08rem;
			}
		}
		hr {
			height: 0.2rem;
			background-color: #f2f2f2;
		}
		.commentInputCon {
			width: 100%;
			height: 0.86rem;
			position: fixed;
			bottom: 0;
			left: 0;
			background: #f5f5f5;
			input,
			.inputBtn {
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0.16rem;
				margin: auto 0;
				width: 5.7rem;
				height: 0.6rem;
				line-height: 0.6rem;
				border-radius: 0.6rem;
				font-size: 0.3rem;
				text-indent: 1em;
				&::-webkit-input-placeholder {
					color: rgb(137, 137, 137);
					text-indent: 1em;
				}
			}
			.addPraiseBtn {
				position: absolute;
				bottom: 0.22rem;
				right: 0.62rem;
			}
		}
    .reclaimPage{
      width:100%;
      height:100%;
    }
	}

    .popup{
      width: 100%;
      height:50%;
      background:rgba(0,0,0,0);
    }
    .addCommentpopup{
      width: 100%;
      height:100%;
      background:rgba(0,0,0,0);
    }
</style>



