<template>
  <transition name="fade" mode="in-out">
    <section class='reclaimCon'>
      <v-Header :comeBack='comeBack'>
        <span slot='text'>下架申请</span>
      </v-Header>
      <textarea placeholder='说点什么好呢...' class='reclaimText' v-model='reclaimText'></textarea>
      <div class='submitBtn' @click='reclaim'>提交</div>
      <mt-popup class='popup' v-model="popupVisible" popup-transition="popup-fade" position="top">提交失败</mt-popup>
    </section>
  </transition>
</template>
<script>
import Header from '../../components/header';
  export default {
    name: '',
    components: {'v-Header':Header},
    props: ['comeBack','reclaimId'],
    data () {
      return {
        reclaimText:'',
        popupVisible:false
      }
    },
    watch: {},
    computed: {},
    created () {  
    },
    methods: {
      reclaim(){
        if(this.reclaimText.length==0) return;    
        this.$http.post(`${this.$port.ARTICLE_DEL}?productId=${this.reclaimId}&reason=${this.reclaimText}`,{
        }).then((response) => {
          const {data} = response;
          if(response.code == 200){
            this.comeBack();
            this.reclaimText = '';
          }else{
            this.popupVisible = true;
            setTimeout(()=>{this.popupVisible = false;},1500);
          }
        })
        .catch((error) => {
          console.log(error.response.data);
          this.popupVisible = true;
          setTimeout(()=>{this.popupVisible = false;},1500);
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
  textarea { 
    resize : none;
    &::-webkit-input-placeholder {
      color:rgb(173,173,173);
      text-indent: 1em;
    }
  }
.reclaimCon{
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:rgb(242,242,242);
  .reclaimText{
    box-sizing: border-box;
    margin:0.1rem 0 0.25rem 0;
    height:3.46rem;
    width:100%;
    text-indent: 1em;
    font-size:0.3rem;
    padding:0.35rem 0 0.35rem 0;
  }
  .submitBtn{
    width:6.9rem;
    height:0.88rem;
    margin:0 auto;
    background:rgb(57,166,206);
    border-radius: 0.15rem;
    font-size:0.36rem;
    line-height:0.88rem;
    color:white;
    text-align:center;
  }
  .popup{
      width: 100%;
      height: 0.86rem;
      background: rgba(0,0,0,0.6);
      text-align: center;
      color: white;
      line-height: 0.86rem;
    }
}
</style>



